import React, { useEffect, useState } from 'react'
import axios from 'axios'
import EquipoId from './EquipoId'
import { Link } from 'react-router-dom'

const EquipoLAIX = () => {
    const [equipo, setEquipo] = useState([])
    const [loading, setLoading] = useState(true)
     useEffect(() => {
        axios.get('https://EquiposLaboratorio.pablojurado2.repl.co/api/equipormn').then(function (response) {
          setEquipo(response.data)
          console.log(response.data)
          setLoading(false)
        });
      }, []) 
  return (
    <>
   {loading ? <div className="bgwhite uk-height-large uk-flex uk-flex-center uk-text-warning uk-text-bold" uk-spinner="ratio: 3"></div> : (
    
    
    <div className="bgwhite ">
    <h1 className="uk-heading-bullet uk-text-center uk-padding">Equipo LAIX</h1>
    <div className="bgwhite uk-flex uk-flex-wrap uk-flex-middle uk-flex-around">
      {equipo.map((e)=>
  
        <div>
        <div uk-tooltip={e.nombre} class="uk-card uk-margin uk-card-default card uk-box-shadow-hover-xlarge ">
            <div class="uk-card-media-top">
            <Link to ={'/equipo/'+e._id}><img src={e.imagen} width="200px" height="50px" alt="" /></Link>
               
            </div>
            <div class="uk-card-body">
                <p  class="uk-text-truncate">{e.nombre}</p>
              {/*   <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.</p> */}
            </div>
        </div>
    </div>
    )}
     
       </div>
       </div>
       
       )} 
       </>
  )
}

export default EquipoLAIX