
import { Outlet, Link, NavLink } from "react-router-dom";
import React from "react";
import Footer from "./Footer";


const Menu = () => {
  
  return (
    <div>
     <nav>
                <div className="uk-flex uk-flex-around uk-padding">
                    <div>
                    <Link to="/"><img src="https://i.imgur.com/DcfDsqN.jpg" /></Link>
                    </div>

                    
                    <Link to="/"><p className="menu">INICIO</p></Link>
                    <Link to="/equipoLAIX"><p className="menu">LAIX</p></Link>
                    <Link to="/equipoNIR"><p className="menu">NIR</p></Link>
                    <Link to="/equipomilwaukee"><p className="menu">MILWAUKEE</p></Link>
                    <Link to="/blogs"><p className="menu">MEDIDORES</p></Link>
                    <Link to="/comunicar"><p className="menu">COMUNICAR</p></Link>
                    <Link to="/"><p className="menu">SERVICIOS</p></Link>
                    <Link to="/"> <p className="menu">SITEMAP</p></Link>


                </div>
            </nav> 
      <Outlet />
      <Footer />
    </div>
  );
};

export default Menu;

