import React from 'react'
import sinresultados from './sinresultados.png'

const NoPage = () => {
  return (
    <div className="bgwhite uk-flex uk-flex-column uk-flex-middle">
        <h1 className="uk-heading-large uk-padding uk-text uk-text-center uk-heading-bullet">SIN RESULTADOS</h1>
        <img className="uk-width-1-4" src={sinresultados} />
    </div>
  )
}

export default NoPage