import React from 'react'
import acercade from './acercade.png'
import celebrar from './celebrar.png'

const AcercaDeNosotros = () => {
    return (
        <div className="bgwhite uk-padding uk-margin">
            <h1 className="uk-text uk-heading-bullet uk-text-center uk-padding">ACERCA DE NOSOTROS</h1>

            <div className="uk-flex uk-flex-center uk-flex-column uk-flex-middle">
                <p className=" uk-width-1-2">es una empresa fundada en 1980 y dedicada a las tcnicas cientficas con especial atencin al campo de Resonancia Magntica Nuclear (RMN). Hemos representado empresas multinacionales como Perkin-Elmer, Hitachi y Bruker en la venta y mantenimiento de aparatos de RMN de alta resolucin. Nuestra actividad principal en los ltimos 20 aos ha sido la representacin de Bruker en la venta y mantenimiento de sus analizadores de RMN de baja resolucin, los Minispec.
                </p>
                <img className=" uk-width-1-5" src={acercade} />
                <p className=" uk-width-1-2">

                    Los tiempos cambian y nuestras representaciones tambin:
                </p>
                <ul className=" uk-width-1-2">
                    <li>Niumag fabricante de Analizadores de RMN y equipos de MRI.</li>
                    <li>FPI fabricante de los Analizadores NIR.</li>
                    <li>Los Analizadores NIR porttiles de Zeltex.</li>
                    <li>Las Balanzas y Bsculas de Ohaus.</li>
                    <li>Los Medidores de Humedad en madera, obra y granos de Gann.</li>
                    <li>Los Medidores Analizadores digitales dedicados al anlisis de la calidad del agua para laboratorios, piscinas y de agricultura de Milwaukee.</li>
                    <li>Una nueva lnea de Medidores Electrnicos econmicos de todo tipo en MODISOFT</li>

                </ul>
                Agradecemos su inters en nuestra empresa
                <h2>

                    T.A.I.S.L.
                </h2>
                <img className=" uk-width-1-5" src={celebrar} />
            </div>
        </div>
    )
}

export default AcercaDeNosotros