import React, { useState } from 'react'
import axios from 'axios'

const CrearCategoria = () => {
  const [nombre1, setNombre1] = useState('')
  const enviar = () => {


    axios
      .post(`https://EquiposLaboratorio.pablojurado2.repl.co/api/categoria/crear`, { nombre: nombre1}).then(function (response) {


        console.log(response)
        alert(response)
      })
  }
  return (
    <div>

      <div className="uk-flex bgwhite uk-padding uk-flex-column uk-flex-center uk-flex-middle">
        <h1>Crear Categoria</h1>
        <input className="uk-input uk-margin" type="text" placeholder='nombre' onChange={(e) => setNombre1(e.target.value)} value={nombre1} id="email" name="nombre"></input>
       
      </div>
      <button onClick={enviar}>Enviar</button>

    </div>
  )
}

export default CrearCategoria