import React, { useEffect, useState } from 'react'
import axios from 'axios'
import swal from 'sweetalert'

const CrearEquipo = () => {
  const [nombre1, setNombre1] = useState('')
  const [descripcion1, setDescripcion1] = useState('')
  const [imagen1, setImagen1] = useState('');
  const [categoria1, setCategoria1] = useState('')
  const [opciones, setOpciones] = useState([])
  
  const enviar = () => {


    axios
      .post(`https://EquiposLaboratorio.pablojurado2.repl.co/api/equipo/crear`, { nombre: nombre1, descripcion: descripcion1, imagen: imagen1, categoria: categoria1 }).then(function (response) {


        swal("Bien!", "Se ha actualizado correctamente", "success");
        setNombre1("")
        setDescripcion1("")
        setImagen1("")
        
      }).catch(function (e){
        swal("Error", "Ha ocurrido un error", "error");
      })
  }
  useEffect(() => {
    axios
      .get(`https://EquiposLaboratorio.pablojurado2.repl.co/api/categorias`).then(function (response) {

        setCategoria1(response.data[0].nombre)
        setOpciones(response.data)
       
      })
  }, [])
  
  return (
    <div>

      <div className="uk-flex bgwhite uk-padding uk-flex-column uk-flex-center uk-flex-middle">
        <h1>Crear Productos</h1>
        <input className="uk-input uk-margin" type="text" placeholder='nombre' onChange={(e) => setNombre1(e.target.value)} value={nombre1} id="email" name="nombre"></input>
        <textarea className="uk-textarea uk-margin" type="text" placeholder='descripcion' onChange={(e) => setDescripcion1(e.target.value)} value={descripcion1} name="descripcion"></textarea>
        <input className="uk-input uk-margin" type="text" placeholder='imagen' onChange={(e) => setImagen1(e.target.value)} value={imagen1} name="imagen"></input>
        <select className="uk-select" onChange={(e) => (console.log(categoria1),setCategoria1(e.target.value))}>
          {opciones.map((e)=><option>{e.nombre}</option>)}
        </select>
       
       {/*  <input className="uk-input uk-margin" type="text" placeholder='categoria' onChange={(e) => setCategoria1(e.target.value)} value={categoria1} name="categoria"></input> */}
       <button className="uk-button uk-margin" onClick={enviar}>Enviar</button>
      </div>
     

    </div>
  )
}

export default CrearEquipo