import React from 'react'
import contacto from './contacto.png'

const Contacto = () => {
  return (
    <div className="bgwhite uk-padding">
      <div className="uk-flex uk-flex-column uk-flex-center uk-flex-middle">
      <h1 className="uk-text uk-text-center uk-heading-medium uk-heading-bullet">CONTACTO</h1>
      <p className="uk-margin uk-text-center uk-text uk-padding uk-width-1-2">Para comunicar con nosotros se puede elegir una de las siguientes modalidades:
        Puede pedirnos cualquier informacin adicional que precise bin por telfono, por e-mail a info@taisl.com o envindonos el siguiente formulario:</p>
        </div>
      <div className="uk-flex uk-flex-middle uk-flex-center">
      <img className="uk-width-1-4"src={contacto} />
      <form className="uk-width-1-4">

    <div class="uk-margin">
        <input class="uk-input  uk-form-width-large uk-form-default" type="text" placeholder="Empresa"  />
    </div>
    <div class="uk-margin">
        <input class="uk-input uk-form-width-large uk-form-default" type="text" placeholder="Contacto"  />
    </div>
    <div class="uk-margin">
        <input class="uk-input uk-form-width-large uk-form-default" type="text" placeholder="Telfono"  />
    </div>
    <div class="uk-margin">
        <input class="uk-input uk-form-width-large uk-form-default" type="text" placeholder="Aparato"  />
    </div>
    <div class="uk-margin">
        <input class="uk-input uk-form-width-large uk-form-default" type="text" placeholder="Email"  />
    </div>
    <div class="uk-margin">
        <textarea class="uk-textarea uk-form-width-large uk-form-default" type="text" placeholder="Detalles"  />
    </div>

    
    <button class="uk-button ">Enviar</button>
</form> 
      </div>
    </div>
  )
}

export default Contacto