import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import ReactMarkdown from 'react-markdown'
import swal from 'sweetalert';
import { Parser } from 'html-to-react'



const EquipoId = ({admin}) => {
    const [nombre, setNombre] = useState('')
    const [imagen, setImagen] = useState('')
    const [categoria, setCategoria] = useState('')
    const [loading, setLoading] = useState(true)
   /*  const [descripcion, setDescripcion] = useState('') */

    const [markdown, setMarkdown] = useState('')
    
    let { id } = useParams();
    useEffect(() => {
      axios.get('https://EquiposLaboratorio.pablojurado2.repl.co/api/findequipo/'+id).then(function (response) {
        setNombre(response.data.nombre)
        setImagen(response.data.imagen)
        setCategoria(response.data.categoria)
        /* setDescripcion(response.data.descripcion) */
        setMarkdown(response.data.descripcion)
        setLoading(false);
        
      });
    }, [])
    const actualizar = ()=>{
      axios.put('https://EquiposLaboratorio.pablojurado2.repl.co/api/actualizarequipo/'+id,{descripcion:markdown}).then(function (response) {
        console.log(response)
        swal("Bien!", "Se ha actualizado correctamente", "success");
        
      }).catch(function (response) {
        swal("Error", "You clicked the button!", "error");
      })
       

      };
      const ocultar = (e) =>{
        document.getElementById("ocultar").style.display = "none";
      }

    
      

      
  return (
    <div className="bgwhite">
      {/*   <h1 className="uk-text-center uk-padding">{nombre}</h1> */}
        <div className="uk-flex uk-flex-column uk-flex-around uk-flex-middle uk-padding">
      {admin ? ( 
        <><div id="ocultar">
        <textarea
                  className="uk-textarea"
                  
                  value={markdown}
                  onChange={(e) => {
                   setMarkdown(e.target.value);
                  }}
                >
                </textarea>
                <button className="uk-button" onClick={actualizar}>Actualizar</button>
                </div>
                <button className="uk-button" onClick={ocultar}>Ocultar</button></>):('')}
      
        {/* <ReactMarkdown className="uk-text-justify uk-width-1-2">{markdown}</ReactMarkdown> */}
        {   loading ? <div className="bgwhite uk-height-large uk-flex uk-flex-center uk-text-warning uk-text-bold" uk-spinner="ratio: 3"></div> :Parser().parse(markdown)}
       {/*  <img className="uk-width-1-4"src={imagen} /> */}
        </div>
        <p>{categoria}</p>
      
      
                
        
    </div>

  )
}

export default EquipoId