import React from 'react'
import App from './App'
import { BrowserRouter, HashRouter, Route, Routes } from "react-router-dom";
import Contacto from './Contacto';
import Menu from './Menu';
import Nav from './Nav';
import NoPage from './NoPage';
import CrearEquipo from './CrearEquipo';
import EquipoId from './EquipoId';
import EquipoRMN from './EquipoLAIX';
import EquipoNIR from './EquipoNIR';
import AcercaDeNosotros from './AcercaDeNosotros';
import ProteccionDatos from './ProteccionDatos';
import ServiciosOfrecidos from './ServiciosOfrecidos';

import EquipoMil from './EquipoMil';
import CrearCategoria from './CrearCategoria';


const Rutas = () => {
  return (
    <div>
    <HashRouter>
        <Routes>
          <Route path="/" element={<Menu/>}>
            <Route index element={<Nav />} />
            <Route path="comunicar" element={<Contacto/>} />
            <Route path="crearequipo" element={<CrearEquipo/>} />
            <Route path="crearcategoria" element={<CrearCategoria/>} />
            <Route path='equipo/:id' element={<EquipoId/>} />
            <Route path='equipo/:id/admin' element={<EquipoId admin={true} />} />
            <Route path='equipoLAIX' element={<EquipoRMN/>} />
            <Route path='equipoNIR' element={<EquipoNIR/>} />
            <Route path='equipomilwaukee' element={<EquipoMil/>} />
            <Route path='acercadenosotros' element={<AcercaDeNosotros/>} />
            <Route path='protecciondedatos' element={<ProteccionDatos/>} />
            <Route path='serviciosofrecidos' element={<ServiciosOfrecidos/>} />
            <Route path="*" element={<NoPage />} />
            
          </Route>
        </Routes>
      </HashRouter>
    </div>
  )
}

export default Rutas